export default class ReactHookFormHandler {
    constructor(reactHookFormContext) {
        this.reactHookFormContext = reactHookFormContext;
    }
    getFormState() {
        const formState = this.reactHookFormContext.formState;
        return {
            isValid: !!formState.isValid,
            isSubmitting: !!formState.isSubmitting,
            isSubmitted: !!formState.isSubmitted,
            hasErrors: Object.keys(formState.errors).length > 0,
        };
    }
    getControlProps(name, options) {
        return this.reactHookFormContext.register(name, options);
    }
    getControlState(name) {
        const formState = this.reactHookFormContext.formState;
        return {
            value: this.reactHookFormContext.getValues(name),
            errors: this.formatFieldErrors(formState.errors[name]),
            isTouched: !!formState.touchedFields[name],
        };
    }
    setFieldValue(name, value, shouldValidate, shouldTouch) {
        this.reactHookFormContext.setValue(name, value, {
            shouldValidate,
            shouldTouch,
        });
    }
    formatFieldErrors(errors) {
        if (!errors) {
            return [];
        }
        const types = typeof (errors === null || errors === void 0 ? void 0 : errors.types) === 'object' ? errors.types : { [errors.type]: errors.message };
        return Object.entries(types).map(([errorType, message]) => {
            // todo : use generic translations when no message is provided
            return typeof message === 'string' && message !== '' ? message : errorType;
        }) || [];
    }
}
